import { AppDispatchType } from "app/store";
import { appActions } from "app/appSlice";
import { BaseResponseType } from "features/todolistsList/api/todolists-api/todolists-api";

/**
 *     .
 * @template T -  ,     .
 * @param {AppDispatchType} dispatch -   Redux,      .
 * @param {BaseResponseType<T>} data -   ,     / .
 * @param {boolean} [isShowGlobalError=true] - , ,      .
 * @return {void} -    
 */
export const handleServerAppError = <T>(
  dispatch: AppDispatchType,
  data: BaseResponseType<T>,
  isShowGlobalError: boolean = true,
) => {
  if (isShowGlobalError) {
    if (data.messages.length) {
      dispatch(appActions.setAppError({ error: data.messages[0] }));
    } else {
      dispatch(appActions.setAppError({ error: "Some error occurred!" }));
    }
  }
  dispatch(appActions.setAppStatus({ status: "failed" }));
};
